@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@400;600&display=swap");
:root {
  --shadow-color: #ddd;
  --img-index: 0;
}

.portfolio-wrapper {
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  grid-template-rows: 2fr 1fr 2fr;
  width: clamp(280px, 100vw, 800px);
  height: min(56.25vw, 450px);
}

.portfolio__display {
  margin: 0 auto;
  position: relative;
  grid-column: 1/4;
  grid-row: 1/4;
  width: 100%;
  height: inherit;
  overflow: hidden;
}

.portfolio__slider {
  display: flex;
  transform: translateX(calc(-100% * var(--img-index)));
  transition: transform 0.2s;
}

.portfolio__display img {
  flex-grow: 0;
  flex-shrink: 0;
  display: block;
  width: 100%;
  height: min(56.25vw, 450px);
  object-fit: cover;
  object-position: left top;
}

.portfolio__arrow {
  grid-column: 1/2;
  grid-row: 2/3;
  justify-self: start;
  background-color: transparent;
  border: none;
  padding: 0;
  margin: 0;
  height: 100%;
  color: white;
  cursor: pointer;
  z-index: 1;
  transition: all 0.3s;
}

.portfolio__arrow--next {
  grid-column: 3/4;
  justify-self: end;
}

.portfolio__arrow svg {
  width: clamp(30px, 10vw, 80px);
  height: clamp(30px, 10vw, 80px);
}

.portfolio__dots {
  display: flex;
  justify-self: center;
  align-self: end;
  grid-column: 1/4;
  grid-row: 3/4;
  margin-bottom: 10px;
  gap: clamp(10px, 4vw, 20px);
  z-index: 1;
}

.portfolio__dot {
  display: inline-block;
  height: clamp(4px, 6vw, 15px);
  width: clamp(4px, 6vw, 15px);
  background-color: white;
  border: none;
  border-radius: 9999vw;
  cursor: pointer;
  box-shadow: 0 2px 5px black;
  transition: background-color 0.2s;
}

.portfolio__dot:hover {
  background-color: #aaa;
}

.portfolio__dot--current,
.portfolio__dot--current:hover {
  background-color: black;
  cursor: default;
}

.portfolio__cover {
  position: absolute;
  inset: 0 0 0 0;
  background: linear-gradient(to top, #ccc 10%, rgba(0, 0, 0, 0) 90%);
  background-size: 100% 1000%;
}

.portfolio__cover--animate {
  animation: bg-pan-left 0.4s ease-in-out;
}

.portfolio__description {
  /* background-color: red; */
  justify-self: center;
  grid-column: 1/4;
  grid-row: 4/5;
}

body,
html {
  min-width: 280px;
  margin: 0;
  font-family: "Quicksand", sans-serif;
}

p,
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}

